body,
div,
h1,
h2,
a,
i,
ul,
li,
footer,
header,
nav {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* make sure to set some focus styles for accessibility */
:focus {
  outline: 0; }

/* HTML5 display-role reset for older browsers */

footer,
header,
nav {
  display: block; }

body {
  line-height: 1; }


ul {
  list-style: none; }

