html {
  color: #222; }

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none; }

::selection {
  background: #b3d4fc;
  text-shadow: none; }

html {
  box-sizing: border-box;
  font-size: 100%; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  background: #212121; }

a {
  color: #f0f0f0; }

.bg-img {
  width: 100%;
  height: 100vh;
  background: #212121 url(/dist/images/bg-md.jpg) center/cover; }

.page-active .icon {
  color: teal; }

.bg-img {
  background: #212121 url(/dist/images/bg-lg.jpg) center/cover; }

.nav {
  width: 100%;
  height: 5rem;
  position: absolute; }

.nav-list {
  width: 60%;
  justify-content: space-between; }

.nav-item {
  padding: 0.5rem;
  position: relative; }
  .nav-item .icon {
    font-size: 1.5rem; }
  .nav-item:hover .square {
    opacity: 1; }

@media only screen and (min-width: 768px) {
  .nav {
    justify-content: flex-end;
    padding: 0 5vw; }
  .nav-list {
    width: 40%; } }

@media only screen and (min-width: 1024px) {
  .nav-list {
    max-width: 20%; } }
